                      <!-- Navigation Links -->\r\n                        <div class=\"hidden space-x-8 sm:ml-10 sm:flex items-center\">\r\n                            <h1 class=\"subheading\"> Eventus </h1>\r\n                        </div>\r\n\r\n                    </div>\r\n\r\n                    <div class=\"hidden sm:flex sm:items-center sm:justify-end sm:ml-6 w-1/2\">\r\n\r\n                        <search-input></search-input>\r\n\r\n                        <!-- Settings Dropdown -->\r\n                        <div class=\"flex items-center ml-3 relative\">\r\n                            <jet-dropdown align=\"left\" width=\"48\">\r\n                                <template #trigger>\r\n                                    <button v-if=\"$page.props.jetstream.managesProfilePhotos\"\r\n                                        class=\"flex items-center text-sm border-2 border-transparent rounded-full focus:outline-none focus:border-transparent transition duration-150 ease-in-out text-white\">\r\n                                        <template v-if=\"$page.props.user\">\r\n                                            <span class=\"capitalize\">Welcome,</span>\r\n                                            <span class=\"ml-1\">{{ $page.props.user.username }}!</span>\r\n                                        </template>\r\n                                        <span class=\"capitalize\" v-else>Welcome, User!</span>\r\n                                        <img class=\"h-8 w-8 rounded-full object-cover ml-3\"\r\n                                            :src=\"$page.props.user.profile_photo_url\" :alt=\"$page.props.user.name\" />\r\n                                    </button>\r\n\r\n                                    <button type=\"button\"\r\n                                        class=\"flex items-center text-sm font-medium text-gray-500 hover:text-gray-700 hover:border-gray-300 focus:outline-none focus:text-gray-700 focus:border-transparent transition duration-150 ease-in-out\"\r\n                                        v-else>\r\n                                        <div>\r\n                                            {{ $page.props.user.username }}\r\n                                        </div>\r\n                                        <svg class=\"ml-2 -mr-0.5 h-4 w-4\" xmlns=\"http://www.w3.org/2000/svg\"\r\n                                            viewBox=\"0 0 20 20\" fill=\"currentColor\">\r\n                                            <path fill-rule=\"evenodd\"\r\n                                                d=\"M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z\"\r\n                                                clip-rule=\"evenodd\" />\r\n                                        </svg>\r\n                                    </button>\r\n                                </template>\r\n\r\n                                <template #content>\r\n                                    <!-- Account Management -->\r\n                                    <div class=\"block px-4 py-2 text-xs text-gray-400\">\r\n                                        Manage Account\r\n                                    </div>\r\n\r\n                                    <jet-dropdown-link :href=\"route('profile.show')\">\r\n                                        Profile\r\n                                    </jet-dropdown-link>\r\n\r\n                                    <div class=\"border-t border-gray-100\"></div>\r\n\r\n                                    <!-- Authentication -->\r\n                                    <form @submit.prevent=\"logout\">\r\n                                        <jet-dropdown-link as=\"button\">\r\n                                            Logout\r\n                                        </jet-dropdown-link>\r\n                                    </form>\r\n                                </template>\r\n                            </jet-dropdown>\r\n\r\n                            <jet-dropdown align=\"right\" width=\"96\">\r\n                                <template #trigger>\r\n                                    <button class=\"mt-1 focus:outline-none\">\r\n                                        <div class=\"flex ml-2 relative\">\r\n                                            <icon name=\"bell\" class=\"w-6 h-6\"></icon>\r\n                                            <template v-if=\"unreadNotifications.length > 0\">\r\n                                                <span\r\n                                                    class=\"text-white text-xs bg-red-600 rounded-full px-2 py-1 absolute bottom-2 left-3\">\r\n                                                    {{ unreadNotifications.length }}\r\n                                                </span>\r\n                                            </template>\r\n                                        </div>\r\n                                    </button>\r\n                                </template>\r\n\r\n                                <template #content v-if=\"notifications.length > 0\">\r\n                                    <inertia-link :href=\"route('notifications.update')\"\r\n                                        class=\"block px-4 py-2 text-sm leading-5 text-gray-700 hover:bg-gray-300 focus:outline-none focus:bg-gray-100 transition duration-150 ease-in-out\">\r\n                                        Mark all as read\r\n                                    </inertia-link>\r\n\r\n                                    <div class=\"border-t border-gray-100\"></div>\r\n\r\n                                    <notifications :unreads=\"unreadNotifications\" :reads=\"readNotifications\">\r\n                                    </notifications>\r\n\r\n                                </template>\r\n\r\n                                <template #content v-else>\r\n                                    <div class=\"block text-center px-4 py-2 text-xs text-gray-400\">\r\n                                        You have 0 notifications\r\n                                    </div>\r\n                                </template>\r\n                            </jet-dropdown>\r\n                        </div>\r\n                    </div>\r\n\r\n                    <!-- Hamburger -->\r\n                    <div class=\"-mr-2 flex items-center sm:hidden\">\r\n                        <button @click=\"showingNavigationDropdown = !showingNavigationDropdown\"\r\n                            class=\"inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-gray-500 hover:bg-gray-100 focus:outline-none focus:bg-gray-100 focus:text-gray-500 transition duration-150 ease-in-out\">\r\n                            <svg class=\"h-6 w-6\" stroke=\"currentColor\" fill=\"none\" viewBox=\"0 0 24 24\">\r\n                                <path\r\n                                    :class=\"{ 'hidden': showingNavigationDropdown, 'inline-flex': !showingNavigationDropdown }\"\r\n                                    stroke-linecap=\"round\" stroke-linejoin=\"round\" stroke-width=\"2\"\r\n                                    d=\"M4 6h16M4 12h16M4 18h16\" />\r\n                                <path\r\n                                    :class=\"{ 'hidden': !showingNavigationDropdown, 'inline-flex': showingNavigationDropdown }\"\r\n                                    stroke-linecap=\"round\" stroke-linejoin=\"round\" stroke-width=\"2\"\r\n                                    d=\"M6 18L18 6M6 6l12 12\" />\r\n                            </svg>\r\n                        </button>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n\r\n            <!-- Responsive Navigation Menu -->\r\n            <div :class=\"{ 'block': showingNavigationDropdown, 'hidden': !showingNavigationDropdown }\" class=\"sm:hidden\">\r\n                <div class=\"pt-2 pb-3 space-y-1\">\r\n                    <jet-responsive-nav-link :href=\"route('dashboard.index')\"\r\n                        :active=\"route().current('dashboard.index')\">\r\n                        Dashboard\r\n                    </jet-responsive-nav-link>\r\n                </div>\r\n\r\n                <!-- Responsive Settings Options -->\r\n                <div class=\"pt-4 pb-1 border-t border-gray-200\">\r\n                    <div class=\"flex items-center px-4\">\r\n                        <div v-if=\"$page.props.jetstream.managesProfilePhotos\" class=\"flex-shrink-0 mr-3\">\r\n                            <img class=\"h-10 w-10 rounded-full object-cover\" :src=\"$page.props.user.profile_photo_url\"\r\n                                :alt=\"$page.props.user.name\" />\r\n                        </div>\r\n\r\n                        <div>\r\n                            <div class=\"font-medium text-base text-gray-800\">{{ $page.props.user.name }}</div>\r\n                            <div class=\"font-medium text-sm text-gray-500\">{{ $page.props.user.email }}</div>\r\n                        </div>\r\n                    </div>\r\n\r\n                    <div class=\"mt-3 space-y-1\">\r\n                        <jet-responsive-nav-link :href=\"route('profile.show')\"\r\n                            :active=\"route().current('profile.show')\">\r\n                            Profile\r\n                        </jet-responsive-nav-link>\r\n\r\n                        <!-- Authentication -->\r\n                        <form method=\"POST\" @submit.prevent=\"logout\">\r\n                            <jet-responsive-nav-link as=\"button\">\r\n                                Logout\r\n                            </jet-responsive-nav-link>\r\n                        </form>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n        </nav>\r\n\r\n        <div class=\"flex-grow flex mt-28 sm:flex-nowrap gap-4\">\r\n    <!-- Page Sidebar -->\r\n    <side-bar></side-bar>\r\n\r\n    <!-- Page Content -->\r\n    <main class=\"bg-gray-300 text-gray-800 shadow rounded mb-9 py-6 px-4 w-full sm:w-2/3 sm:mx-2 sm:mb-9 md:mx-4 md:px-10 lg:mx-auto font-sans\">\r\n        <slot></slot>\r\n    </main>\r\n</div>\r\n\r\n\r\n\r\n\r\n\r\n        <footer class=\"footer bg-gray-800 text-white py-4\">\r\n            <p class=\"text-center\">&copy; {{ currentYear }} Eventus. All Rights Reserved.</p>\r\n        </footer>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport JetDropdown from '@/Jetstream/Dropdown'\r\nimport JetDropdownLink from '@/Jetstream/DropdownLink'\r\nimport JetNavLink from '@/Jetstream/NavLink'\r\nimport JetResponsiveNavLink from '@/Jetstream/ResponsiveNavLink'\r\nimport Notifications from '@/Components/Notifications'\r\nimport SideBar from '@/Components/SideBar'\r\n\r\n// search components \r\nimport SearchInput from '../Components/SearchInput'\r\n\r\nexport default {\r\n    components: {\r\n        JetDropdown,\r\n        JetDropdownLink,\r\n        JetNavLink,\r\n        JetResponsiveNavLink,\r\n        Notifications,\r\n        SideBar,\r\n        SearchInput,\r\n    },\r\n\r\n\r\n\r\n    data() {\r\n        return {\r\n            showingNavigationDropdown: false,\r\n            unreadNotifications: this.$page.props.auth.unreadNotifications,\r\n            readNotifications: this.$page.props.auth.readNotifications,\r\n            notifications: this.$page.props.auth.notifications,\r\n        }\r\n    },\r\n\r\n    computed: {\r\n        currentYear() {\r\n            return new Date().getFullYear();\r\n        }\r\n\r\n    },\r\n\r\n    mounted() {\r\n        this.listen()\r\n    },\r\n\r\n    methods: {\r\n        listen() {\r\n            Echo.private(`App.Models.User.${this.$page.props.user.id}`)\r\n                .notification((notification) => {\r\n                    let newUnreadNotifications = {\r\n                        data: {\r\n                            info: {\r\n                                avatar: notification.info.avatar,\r\n                                message: notification.info.message,\r\n                                link: notification.info.link,\r\n                                sent: notification.info.sent,\r\n                            }\r\n                        },\r\n                        id: notification.id\r\n                    }\r\n                    this.unreadNotifications.push(newUnreadNotifications)\r\n                    this.notifications.push(newUnreadNotifications)\r\n                })\r\n        },\r\n        logout() {\r\n            this.$inertia.post(route('logout'));\r\n        },\r\n    }\r\n}\r\n\r\n</script>\r\n\r\n<style>\r\nbody.swal2-toast-shown .swal2-container.swal2-top-end,\r\nbody.swal2-toast-shown .swal2-container.swal2-top-right {\r\n    top: 60px;\r\n}\r\n\r\n.homepage {\r\n    background-image: url('/storage/images/background.png');\r\n    background-size: cover;\r\n    font-family: sans-serif;\r\n    background-position: center;\r\n}\r\n\r\n\r\n.logo-dashboard {\r\n    width: 85px;\r\n    height: 80px;\r\n    position: absolute;\r\n    top: 10px;\r\n    left: 10px;\r\n    border: 4px solid white;\r\n    border-radius: 50%;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.subheading {\r\n    font-size: 54px;\r\n    font-weight: bold;\r\n    color: white;\r\n    font-style: oblique;\r\n}\r\n\r\n\r\n\r\n.footer {\r\n    width: 100%;\r\n    text-align: center;\r\n    font-size: 14px;\r\n    color: rgb(0, 0, 0);\r\n    font-weight: bold;\r\n    background-color: #d0cbcb;\r\n    /* Footer background */\r\n    padding: 5px 0;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
