___CSS_LOADER_EXPORT___.push([module.id, ".loginpage{background-image:url(/storage/images/background.png);background-position:50%;background-repeat:no-repeat;background-size:cover;justify-content:space-between;min-height:100vh;padding-top:100px}.header-container,.loginpage{align-items:center;display:flex;flex-direction:column}.header-container{margin-bottom:20px}.login-subtitle{color:#000;font-weight:700}.logo{border:4px solid #fff;box-sizing:border-box;height:80px;left:10px;position:absolute;top:10px;width:85px}.divider,.logo{border-radius:50%}.divider{background-color:#000;display:inline-block;height:5px;width:600px}.account-link{border-radius:5px;color:#000;float:left;font-size:15px;font-weight:700;height:6%;margin-left:14%;padding:5px;text-decoration:underline}.login-title,h1{color:#000;font-size:420%;font-weight:700}.loginform{background-color:hsla(0,0%,70%,.9);border-radius:10px;box-shadow:0 0 10px rgba(0,0,0,.2);box-sizing:border-box;color:#000;font-size:15px;margin-bottom:40px;max-height:700px;overflow-y:auto;padding:1rem;position:center;width:29%}.loginbutton{background-color:#000;border:none;border-radius:5px;color:#fff;cursor:pointer;float:right;font-size:15px;padding:8px 15px}.email,.password{color:#000;float:left;font-weight:700}.regis{margin-bottom:10px}.footer,.regis{color:#000;font-weight:700;text-align:center}.footer{background-color:#d0cbcb;font-size:14px;margin-top:10px;padding:5px 0;position:relative;width:100%}.forgotpassword,.remember-me{color:#000;font-size:14px;font-weight:700}.forgotpassword{text-align:center}.forgot-link{color:#000;font-size:14px;text-decoration:underline}.input-field-dark{background-color:#000!important;border:1px solid #333;color:#fff!important}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Auth/Login.vue"],"names":[],"mappings":"AAgHA,WACE,oDAAuD,CAGvD,uBAAkC,CADlC,2BAA4B,CAD5B,qBAAsB,CAMtB,6BAA8B,CAH9B,gBAAiB,CAKjB,iBACF,CAEA,6BAJE,kBAAmB,CAHnB,YAAa,CACb,qBAWF,CALA,kBAII,kBACJ,CAEA,gBACE,UAAc,CACd,eACF,CACA,MAMI,qBAAuB,CAEvB,qBAAsB,CANtB,WAAY,CAGZ,SAAU,CAFV,iBAAkB,CAClB,QAAS,CAHT,UAQJ,CAEA,eAJI,iBAUJ,CANA,SAGE,qBAAuB,CACvB,oBAAqB,CAFrB,UAAW,CADX,WAKF,CAEA,cAQE,iBAAkB,CAPlB,UAAY,CACZ,UAAW,CAIX,cAAe,CAFf,eAAiB,CADjB,SAAU,CAMV,eAAgB,CAFhB,WAAY,CAFZ,yBAKF,CASA,gBAJE,UAAmB,CAFnB,cAAe,CACf,eASF,CAEA,WACE,kCAA0C,CAW1C,kBAAmB,CAFnB,kCAA2C,CAP3C,qBAAsB,CAKtB,UAAc,CAFd,cAAe,CAKf,kBAAmB,CAPnB,gBAAiB,CACjB,eAAgB,CAIhB,YAAa,CAFb,eAAgB,CALhB,SAWF,CAEA,aAEE,qBAAuB,CAKvB,WAAY,CACZ,iBAAkB,CALlB,UAAY,CAGZ,cAAe,CAFf,WAAY,CAHZ,cAAe,CAIf,gBAIF,CAEA,iBAEE,UAAc,CACd,UAAW,CACX,eACF,CAIA,OAIE,kBACF,CAEA,eANE,UAAc,CACd,eAAiB,CACjB,iBAeF,CAXA,QAKE,wBAAyB,CAFzB,cAAe,CAMf,eAAgB,CAFhB,aAAc,CACd,iBAAkB,CAPlB,UAUF,CAQA,6BALE,UAAc,CACd,cAAe,CACf,eAOmB,CAJrB,gBAIE,iBAAmB,CAErB,aACE,UAAc,CAEd,cAAe,CADf,yBAEF,CAGA,kBAEE,+BAAkC,CAClC,qBAAsB,CAFtB,oBAGF","sourcesContent":["<template>\r\n  <img :src=\"'/storage/images/logo.png'\" alt=\"logo\" class=\"logo\">\r\n  <center>\r\n    <div class=\"loginpage font-sans\">\r\n          <center>\r\n              <div class=\"header-container\">                    \r\n                <h1 class=\"login-title\"> Eventus </h1>\r\n                <span class=\"divider\"></span>\r\n              </div>\r\n          </center>\r\n          \r\n          <form @submit.prevent=\"submit\" class=\"loginform\">\r\n              <jet-validation-errors class=\"mb-4\" />\r\n              <h2 class=\"regis\">Login Page</h2>\r\n              <p class=\"login-subtitle\"> Enter your details to log in to your account</p>\r\n\r\n              <div>\r\n                <br>\r\n                  <jet-label for=\"email\" value=\"Email\" class=\"email\"/>\r\n                  <jet-input id=\"email\" type=\"email\" class=\"mt-1 block w-full rounded-md input-field-dark\" v-model=\"form.email\" required autofocus />\r\n              </div>\r\n\r\n              <div class=\"mt-4\">\r\n                  <jet-label for=\"password\" value=\"Password\" class=\"password\" />\r\n                  <jet-input id=\"password\" type=\"password\" class=\"mt-1 block w-full rounded-md input-field-dark\" v-model=\"form.password\" required autocomplete=\"current-password\" />\r\n              </div>\r\n\r\n              <div class=\"block mt-4\">\r\n                  <label class=\"flex items-center remember-me\">\r\n                      <jet-checkbox name=\"remember\" v-model=\"form.remember\" />\r\n                      <span class=\"ml-2\">Remember me</span>\r\n                  </label>\r\n              </div>\r\n\r\n              <div class=\"mt-4 forgotpassword\">\r\n                  <inertia-link v-if=\"canResetPassword\" :href=\"route('password.request')\" class=\"forgot-link\">\r\n                      Forgot Password?\r\n                  </inertia-link>\r\n              </div>\r\n            \r\n\r\n              <inertia-link :href=\"route('register')\" class=\"account-link\">\r\n                  Not registered yet? REGISTER\r\n              </inertia-link>\r\n              <jet-button class=\"loginbutton rounded-md\" :class=\"{ 'opacity-25': form.processing }\" :disabled=\"form.processing\">\r\n                  LOG IN\r\n              </jet-button>\r\n\r\n          </form>\r\n          <footer class=\"footer\">\r\n              <p>&copy; {{ new Date().getFullYear() }} Eventus. All Rights Reserved.</p>\r\n          </footer>\r\n      </div>\r\n  </center>\r\n</template>\r\n\r\n<script>\r\n  import JetAuthenticationCard from '@/Jetstream/AuthenticationCard'\r\n  import JetButton from '@/Jetstream/Button'\r\n  import JetInput from '@/Jetstream/Input'\r\n  import JetCheckbox from '@/Jetstream/Checkbox'\r\n  import JetLabel from '@/Jetstream/Label'\r\n  import JetValidationErrors from '@/Jetstream/ValidationErrors'\r\n\r\n  export default {\r\n      components: {\r\n          JetAuthenticationCard,\r\n          JetButton,\r\n          JetInput,\r\n          JetCheckbox,\r\n          JetLabel,\r\n          JetValidationErrors\r\n      },\r\n\r\n      props: {\r\n          canResetPassword: Boolean,\r\n          status: String\r\n      },\r\n\r\n      data() {\r\n          return {\r\n              form: this.$inertia.form({\r\n                  email: '',\r\n                  password: '',\r\n                  remember: false\r\n              })\r\n          }\r\n      },\r\n\r\n\r\n      computed: {\r\n          currentYear() {\r\n              return new Date().getFullYear();\r\n          }\r\n\r\n      },\r\n      methods: {\r\n          submit() {\r\n              this.form\r\n                  .transform(data => ({\r\n                      ... data,\r\n                      remember: this.form.remember ? 'on' : ''\r\n                  }))\r\n                  .post(this.route('login'), {\r\n                      onFinish: () => this.form.reset('password'),\r\n                  })\r\n          }\r\n      }\r\n  }\r\n</script>\r\n\r\n<style>\r\n.loginpage {\r\n  background-image: url('/storage/images/background.png');\r\n  background-size: cover;\r\n  background-repeat: no-repeat;\r\n  background-position: center center;\r\n  min-height: 100vh;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: space-between; \r\n  align-items: center;\r\n  padding-top: 100px;\r\n}\r\n\r\n.header-container{ \r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    margin-bottom: 20px; \r\n}\r\n\r\n.login-subtitle {\r\n  color: #000000;\r\n  font-weight: bold;\r\n}\r\n.logo {\r\n    width: 85px;\r\n    height: 80px;\r\n    position: absolute;\r\n    top: 10px;\r\n    left: 10px;\r\n    border: 4px solid white;\r\n    border-radius: 50%;\r\n    box-sizing: border-box;\r\n}\r\n\r\n.divider {\r\n  width: 600px;\r\n  height: 5px;\r\n  background-color: black;\r\n  display: inline-block;\r\n  border-radius: 50%;\r\n}\r\n\r\n.account-link {\r\n  color: black;\r\n  float: left;\r\n  height: 6%;\r\n  font-weight: bold;\r\n  text-decoration: underline;\r\n  font-size: 15px;\r\n  padding: 5px;\r\n  border-radius: 5px;\r\n  margin-left: 14%;\r\n}\r\n\r\nh1 {\r\n  font-size: 420%;\r\n  font-weight: bold;\r\n  color: rgb(0, 0, 0);\r\n  \r\n}\r\n\r\n.login-title {\r\n  font-size: 420%; \r\n  font-weight: bold;\r\n  color: rgb(0, 0, 0); \r\n}\r\n\r\n.loginform {\r\n  background-color: rgba(178, 178, 178, 0.9);\r\n  width: 29%;\r\n  box-sizing: border-box;\r\n  max-height: 700px;\r\n  overflow-y: auto;\r\n  font-size: 15px;\r\n  position: center;\r\n  color: #000000;\r\n  padding: 1rem;\r\n  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);\r\n  margin-bottom: 40px;\r\n  border-radius: 10px;\r\n}\r\n\r\n.loginbutton {\r\n  font-size: 15px;\r\n  background-color: black;\r\n  color: white;\r\n  float: right;\r\n  padding: 8px 15px;\r\n  cursor: pointer;\r\n  border: none;\r\n  border-radius: 5px;\r\n}\r\n\r\n.email,\r\n.password {\r\n  color: #000000;\r\n  float: left;\r\n  font-weight: bold;\r\n}\r\n\r\n\r\n\r\n.regis {\r\n  color: #000000;\r\n  font-weight: bold;\r\n  text-align: center;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.footer {\r\n  width: 100%;\r\n  text-align: center;\r\n  font-size: 14px;\r\n  font-weight: bold;\r\n  background-color: #d0cbcb;\r\n  color: rgb(0, 0, 0);\r\n  padding: 5px 0;\r\n  position: relative;\r\n  margin-top: 10px;\r\n\r\n}\r\n\r\n.remember-me {\r\n  color: #000000;\r\n  font-size: 14px;\r\n  font-weight: bold;\r\n}\r\n\r\n.forgotpassword {\r\n  color: #000000;\r\n  font-weight: bold;\r\n  font-size: 14px;\r\n  text-align: center;}\r\n\r\n.forgot-link {\r\n  color: #000000;\r\n  text-decoration: underline;\r\n  font-size: 14px;\r\n}\r\n\r\n/* Style for the input field text and background */\r\n.input-field-dark {\r\n  color: white !important;\r\n  background-color: black !important;\r\n  border: 1px solid #333;\r\n}\r\n</style>\r\n\r\n\r\n"],"sourceRoot":""}]);
