___CSS_LOADER_EXPORT___.push([module.id, ".resetpass{background-image:url(/storage/images/background.png);background-position:50%;background-repeat:no-repeat;background-size:cover;justify-content:space-between;min-height:100vh;padding-bottom:0}.header-container,.resetpass{align-items:center;display:flex;flex-direction:column}.header-container{margin-bottom:20px;margin-top:100px}.logo{border:4px solid #fff;box-sizing:border-box;height:80px;left:10px;position:absolute;top:10px;width:85px}.divider,.logo{border-radius:50%}.divider{background-color:#000;display:inline-block;height:5px;width:600px}h1{color:#000;font-size:400%;font-weight:700}.form-container{align-items:flex-start;display:flex;justify-content:center;width:100%}.forgot-password-form{background-color:hsla(0,0%,70%,.9);border-radius:10px;box-shadow:0 0 10px rgba(0,0,0,.2);box-sizing:border-box;color:#000;font-size:16px;max-height:700px;overflow-y:auto;padding:1rem;width:29%}.forgot-password-button{background-color:#000;border:none;border-radius:5px;color:#fff;cursor:pointer;float:right;font-size:15px;padding:8px 15px}.email{color:#000;float:left;font-size:16px;font-weight:700}.input-field-dark{background-color:#000!important;border:1px solid #333;color:#fff!important}.regis,p{color:#000;font-weight:700}.regis{margin-bottom:10px}.footer,.regis{text-align:center}.footer{background-color:#d0cbcb;color:#fff;font-size:14px;font-weight:700;margin-top:auto;padding:5px 0;width:100%}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Auth/ForgotPassword.vue"],"names":[],"mappings":"AAiEA,WACE,oDAAuD,CAGvD,uBAAkC,CADlC,2BAA4B,CAD5B,qBAAsB,CAOtB,6BAA8B,CAJ9B,gBAAiB,CAKjB,gBACA,CAEF,6BALE,kBAAmB,CAFnB,YAAa,CACb,qBAYF,CANA,kBAKE,kBAAmB,CADnB,gBAEF,CACA,MAMI,qBAAuB,CAEvB,qBAAsB,CANtB,WAAY,CAGZ,SAAU,CAFV,iBAAkB,CAClB,QAAS,CAHT,UAQJ,CACA,eAHI,iBASJ,CANA,SAGA,qBAAuB,CACvB,oBAAqB,CAFrB,UAAW,CADX,WAKA,CACA,GAGE,UAAmB,CAFnB,cAAe,CACf,eAEF,CAEA,gBAGI,sBAAuB,CAFvB,YAAa,CACb,sBAAuB,CAEvB,UACJ,CAEA,sBACE,kCAA0C,CAS1C,kBAAmB,CADnB,kCAA2C,CAN3C,qBAAsB,CAItB,UAAc,CADd,cAAe,CAFf,gBAAiB,CACjB,eAAgB,CAGhB,YAAa,CANb,SASF,CAEA,wBAEE,qBAAuB,CAKvB,WAAY,CACZ,iBAAkB,CALlB,UAAY,CAGZ,cAAe,CAFf,WAAY,CAHZ,cAAe,CAIf,gBAIF,CAEA,OACE,UAAc,CACd,UAAW,CAEX,cAAe,CADf,eAEF,CAGA,kBAEE,+BAAkC,CAClC,qBAAsB,CAFtB,oBAGF,CAKA,SAHE,UAAc,CACd,eAOF,CALA,OAIE,kBACF,CAEA,eAJE,iBAaF,CATA,QAMI,wBAAyB,CAFzB,UAAY,CADZ,cAAe,CAEf,eAAiB,CAGjB,eAAgB,CADhB,aAAc,CANd,UAQJ","sourcesContent":["<template>\r\n  <div class=\"resetpass font-sans\">\r\n      <img :src=\"'/storage/images/logo.png'\" alt=\"logo\" class=\"logo\">\r\n      <div class=\"header-container\">\r\n        <h1> Eventus </h1>\r\n        <span class=\"divider\"></span><br>\r\n      </div>\r\n     <div class=\"form-container\">\r\n        <div class=\"forgot-password-form\">\r\n            <h2 class=\"regis\">Forgot Password</h2>\r\n            <p>  Simply provide your email, and we'll send you a link to create a new one.</p>\r\n            <br>\r\n            <form @submit.prevent=\"submit\" >\r\n              <jet-validation-errors class=\"mb-4\" />\r\n                    <jet-label for=\"email\" value=\"Email\" class=\"email\" />\r\n                    <jet-input id=\"email\" type=\"email\" class=\"mt-1 block w-full rounded-md input-field-dark\" v-model=\"form.email\" required autofocus />\r\n                    <br><br>\r\n                    <jet-button class=\"forgot-password-button rounded-md\" :class=\"{ 'opacity-25': form.processing }\" :disabled=\"form.processing\">  Email Password Reset Link\r\n                    </jet-button>\r\n            </form>\r\n        </div>\r\n      </div>\r\n      <footer class=\"footer\">\r\n        <p>&copy; {{ new Date().getFullYear() }} Eventus. All Rights Reserved.</p>\r\n      </footer>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\n  import JetAuthenticationCard from '@/Jetstream/AuthenticationCard'\r\n  import JetButton from '@/Jetstream/Button'\r\n  import JetInput from '@/Jetstream/Input'\r\n  import JetLabel from '@/Jetstream/Label'\r\n  import JetValidationErrors from '@/Jetstream/ValidationErrors'\r\n\r\n  export default {\r\n      components: {\r\n          JetAuthenticationCard,\r\n          JetButton,\r\n          JetInput,\r\n          JetLabel,\r\n          JetValidationErrors\r\n      },\r\n\r\n      props: {\r\n          status: String\r\n      },\r\n\r\n      data() {\r\n          return {\r\n              form: this.$inertia.form({\r\n                  email: ''\r\n              })\r\n          }\r\n      },\r\n\r\n      methods: {\r\n          submit() {\r\n              this.form.post(this.route('password.email'))\r\n          }\r\n      }\r\n  }\r\n</script>\r\n\r\n<style>\r\n.resetpass{\r\n  background-image: url('/storage/images/background.png');\r\n  background-size: cover;\r\n  background-repeat: no-repeat;\r\n  background-position: center center;\r\n  min-height: 100vh;\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: space-between; /* Push content to top and bottom */\r\n  padding-bottom: 0; /* Remove bottom padding from here */\r\n  }\r\n\r\n.header-container {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  margin-top: 100px;\r\n  margin-bottom: 20px;\r\n}\r\n.logo {\r\n    width: 85px;\r\n    height: 80px;\r\n    position: absolute;\r\n    top: 10px;\r\n    left: 10px;\r\n    border: 4px solid white;\r\n    border-radius: 50%;\r\n    box-sizing: border-box;\r\n}\r\n.divider{\r\nwidth: 600px;\r\nheight: 5px;\r\nbackground-color: black;\r\ndisplay: inline-block;\r\n  border-radius: 50%;\r\n}\r\nh1{\r\n  font-size: 400%;\r\n  font-weight: bold;\r\n  color: rgb(0, 0, 0);\r\n}\r\n\r\n.form-container{\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: flex-start;\r\n    width: 100%;\r\n}\r\n\r\n.forgot-password-form {\r\n  background-color: rgba(178, 178, 178, 0.9);\r\n  width: 29%;\r\n  box-sizing: border-box;\r\n  max-height: 700px;\r\n  overflow-y: auto;\r\n  font-size: 16px;\r\n  color: #000000;\r\n  padding: 1rem;\r\n  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);\r\n  border-radius: 10px;\r\n}\r\n\r\n.forgot-password-button { /* Changed class name here */\r\n  font-size: 15px;\r\n  background-color: black;\r\n  color: white;\r\n  float: right;\r\n  padding: 8px 15px;\r\n  cursor: pointer;\r\n  border: none;\r\n  border-radius: 5px;\r\n}\r\n\r\n.email{\r\n  color: #000000;\r\n  float: left;\r\n  font-weight: bold;\r\n  font-size: 16px;\r\n}\r\n\r\n/* Style for the input field text and background */\r\n.input-field-dark {\r\n  color: white !important;\r\n  background-color: black !important;\r\n  border: 1px solid #333;\r\n}\r\np{\r\n  color: #000000;\r\n  font-weight: bold;\r\n}\r\n.regis{\r\n  color: #000000;\r\n  font-weight: bold;\r\n  text-align: center;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n.footer {\r\n    width: 100%;\r\n    text-align: center;\r\n    font-size: 14px;\r\n    color: white;\r\n    font-weight: bold;\r\n    background-color: #d0cbcb;\r\n    padding: 5px 0;\r\n    margin-top: auto; /* Push footer to the bottom */\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
