order-box;\r\n    /* Add these lines to center children */\r\n    display: flex;\r\n    justify-content: center; /* center horizontally */\r\n    align-items: center;     /* center vertically */\r\n    flex-direction: column;  /* stack things vertically inside */\r\n    padding-top: 105px\r\n}\r\n\r\n\r\n.logo {\r\n    width: 85px;\r\n    height: 80px;\r\n    position: absolute;\r\n    top: 10px;\r\n    left: 10px;\r\n    border: 4px solid white;\r\n    border-radius: 50%;\r\n    box-sizing: border-box;\r\n}\r\n  .btn1 {\r\n    font-size: 350%;\r\n    padding: 16px 35px;\r\n    background-color: darkgray;\r\n    color: black;\r\n    font-weight: bolder;\r\n    border-radius: 14px;\r\n    border: aliceblue;\r\n    border: 1px solid aliceblue;\r\n}\r\n\r\n\r\n.btn1:hover {\r\n    background-color: #595d61a2;\r\n    color: rgb(0, 0, 0);\r\n    transition: all 0.3s ease;\r\n    transform: scale(1.05);\r\n}\r\n\r\n\r\n.divider{\r\n  width: 600px;\r\n  height: 5px;\r\n  background-color: black;\r\n  display: inline-block;\r\n  border-radius: 50%;\r\n}\r\n  h1{\r\n    font-size: 500%;\r\n    font-weight: bold;\r\n    color: rgb(18, 17, 17);\r\n}\r\n\r\n.welcome-title {\r\n        font-size: 500%;\r\n        font-weight: bold;\r\n        color: black; \r\n    }\r\n\r\n\r\n.button-group {\r\n    display: flex;\r\n    justify-content: center; /* center horizontally */\r\n    gap: 50px; /* space between buttons */\r\n}\r\n\r\n.title-box {\r\n    background-color: rgba(167, 156, 156, 0.751); /* White box with slight transparency */\r\n    padding: 40px;\r\n    border-radius: 20px;\r\n    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Soft shadow */\r\n    display: inline-block; /* So it hugs the content */\r\n    text-align: center;\r\n    margin-top: 20px;\r\n}\r\n\r\n\r\n  .activemembers {\r\n    font-size: 22px;\r\n    color: white;\r\n    font-weight: bolder;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.footer {\r\n    width: 100%;\r\n    text-align: center;\r\n    font-size: 14px;\r\n    color: rgb(0, 0, 0);\r\n    font-weight: bold;\r\n    background-color: #d0cbcb;\r\n    /* Footer background */\r\n    padding: 5px 0;\r\n}\r\n</style>"],"sourceRoot":""}]);
